.form_div{
  margin: 0;
  padding: 20px;
  background: white;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  height: 20%;
  width: 60%;
  text-align: center;
  box-shadow: 10px 10px 0px -3px rgba(0, 255, 25, 0.45);
    -webkit-box-shadow: 10px 10px 0px -3px rgba(0, 255, 25, 0.45);
    -moz-box-shadow: 10px 10px 0px -3px rgba(0, 255, 25, 0.45);

}
.label_style{
  font-size: 20px;
  font-style: normal;
  font-weight: bold;
  
}
.url_class{
  color: rgba(0, 74, 7, 0.45);
  border-color: rgba(0, 255, 25, 0.45);
  border-width: 1px;
  border-radius: 5px;
  height: 25px;
  width: 80%;
}
form{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.btn{
  width: 30%;
  height: 30px;
  background-color: rgba(0, 255, 25, 0.45);
  border: none;
  border-radius: 5px;
  color: rgb(0, 99, 0);
  font-weight: 600;
}
.message{
  color: rgb(0, 27, 137);
  font-weight: 500;
}